	module.exports = {"data":{"markdownRemark":{"html":"<p>Concentrate on reading the French scholar Alan Perefett's \"The Conflict between the Two Worlds of the Stagnation Empire.\" Read from morning to evening. Because I was impulsive to write a book review, I read it carefully. This is a very analytical work. The material is the history of the British special envoy to China. The materials are sufficient. He mastered the diaries of the people who came at that time and checked the archives of the Qing Dynasty. Analysis in the modern context, trying to understand the huge cultural existence of China. He has published many discussions in historical analysis, targeting contemporary China. Perefit is a well-known French scholar and an official who has been to Fudan University. His \"French disease\" was translated into Chinese as \"the evils of bureaucracy\", and his book \"When the Sleeping Lions Wake Up\" is also about China. His judgment and analytical skills are strong and impressive.</p>\n<p>As can be seen from his book, the civilizations of the two worlds were at the time of conflict and the background of today's conflicts. At that time, China should be said to be in front of the development of the world. Although the countries of Western Europe have developed in science and technology, industrial production capacity has greatly improved. But China still has certain advantages, but it is losing its advantage. China since modern times</p>\n<p>The backward process has begun, and the main reason is that he seems to be the conservative and conservative rule of the Chinese rulers. For the visit of the British special envoy, the emperor personally asked, local officials reported their activities to the emperor. The emperor’s main mentality was to treat them as tribute and emphasize their obedience to the Chinese system. This is a long struggle. The other is to disregard the Western modern science and technology, the Chinese rulers. The artillery brought by the special envoy Majerni, the emperor did not even look at it, and it was useless. He wrote that if the Chinese emperor paid more attention to these artillery at that time, the history of China in the future might be different, and the Chinese would not necessarily fail in the Opium War. Ma said that as long as two warships can defeat China's navy at the time, it is probably not exaggerated.</p>\n<p>The basic contradiction here is the contradiction between culture and technology, until later China is still arguing over the issue of physical use. In a country with a long history of culture, it is always not easy to change. In countries where the cultural base is too large, it is even harder to innovate. China has the conditions for these two aspects. How to change? It is the mystery of history.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0066.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0066.md","frontmatter":{"title":"April 3 Sundays"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0066.md absPath of file >>> MarkdownRemark"}}

